import React, { useState } from 'react';
import './MCQCard.css'; // Import the CSS file

const MCQCard = ({ MCQ }) => {
  const [selectedOptions, setSelectedOptions] = useState({});
  const [score, setScore] = useState(null);

  const handleOptionSelect = (questionId, option) => {
    setSelectedOptions({
      ...selectedOptions,
      [questionId]: option,
    });
  };

  const handleSubmit = () => {
    let currentScore = 0;

    MCQ.forEach((question) => {
      if (selectedOptions[question.id] === question.correctAnswer) {
        currentScore++;
      }
    });

    setScore(currentScore);
  };

  return (
    <div className="mcq-card-container">
      {MCQ.map((question) => (
        <div className="card" key={question.id}>
          <h2 className="question-heading">Question {question.id}</h2>
          <p className="question-text">{question.question}</p>
          <ul className="options-list">
            {question.options.map((option, index) => (
              <li className="option" key={index}>
                <label>
                  <input
                    className="option-input"
                    type="radio"
                    name={`question_${question.id}`}
                    value={option}
                    checked={selectedOptions[question.id] === option}
                    onChange={() => handleOptionSelect(question.id, option)}
                  />
                  {option}
                </label>
              </li>
            ))}
          </ul>
        </div>
      ))}
      <button className="submit-button" onClick={handleSubmit}>Submit</button>
      {score !== null && <p className="score-paragraph">Your Score: {score}/{MCQ.length}</p>}
    </div>
  );
};

export default MCQCard;
