import React, { useState, useEffect } from 'react';
import { ApiRequest } from '../Api/ApiService';
import './Writing.css'; // Import the CSS file

const Writing = ({topic}) => {
  // const [topic, setTopic] = useState('');
  // const [wordLimit, setWordLimit] = useState(100);
  const [userText, setUserText] = useState('');
  const [assessment, setAssessment] = useState(null);
  const [loading, setLoading] = useState(false);

  // const getRandomTopic = async () => {
  //   const query = 'I want you to generate a random general topic for English writing practice, also provide the word limit in the topic, but not more than 500. And the format for this would be "Topic:" Your topic". Word-limit:" Your word limit. And generate the result in this format only, not an extra single character.';
  //   try {
  //     const response = await ApiRequest(query);
  //     setTopic(response);
  //   } catch (error) {
  //     console.log(error);
  //   }
  // };

  const assessText = async () => {
    if(userText === ''){
      setAssessment("There is nothing to assess.");
    }

    else{
    const query = `Assess '${userText}' for grammar and spelling mistakes and for the relevancy and meaningfulness of this '${userText}' content with respect to this ${topic}, also for the word limit given in the ${topic}.`;
    console.log(query);
    setLoading(true);
    try {
      const response = await ApiRequest(query);
      console.log(response);
      setAssessment(response);
    } catch (error) {
      console.error('Error assessing text:', error);
    }
    setLoading(false);
  }
  };

  // useEffect(() => {
  //   getRandomTopic();
  // }, []);

  return (
    <div className="writing-container"> {/* Apply container style */}
      <h2 className="writing-heading">English Writing Practice</h2>
      <div className="topic-container"> {/* Apply topic container style */}
        <h3>{topic}</h3>
        <p></p>
      </div>
      <div className="textarea-container"> {/* Apply textarea container style */}
        <h3>Write Here:</h3>
        <textarea
          className="textarea-input" 
          rows="6"
          cols="50"
          value={userText}
          onChange={(e) => setUserText(e.target.value)}
        />
      </div>
      <div className="button-container"> {/* Apply button container style */}
        <button className="assess-button" onClick={assessText} disabled={loading}> {/* Apply button style */}
          {loading ? 'Assessing...' : 'Assess'}
        </button>
      </div>
      {assessment && (
        <div className="assessment-container"> {/* Apply assessment container style */}
          <h3 className="assessment-heading">Assessment:</h3> {/* Apply assessment heading style */}
          <pre className="assessment-result">{assessment}</pre> {/* Apply assessment result style */}
        </div>
      )}
    </div>
  );
};

export default Writing;
