import React, { useState, useRef, useEffect } from 'react';
import { chatService } from '../Api/chatSerivce';
import './Speaking.css';
import { SpinnerGap } from 'phosphor-react';

const Speaking = () => {
  const [userInput, setUserInput] = useState('');
  const [aiResponse, setAiResponse] = useState('');
  const [speaking, setSpeaking] = useState(false); // State to track speech synthesis
  const [processing, setProcessing] = useState(false); // State to track AI processing
  const recognition = new window.webkitSpeechRecognition(); // for Chrome
  const [conversationHistory, setConversationHistory] = useState([]);
  const chatContainerRef = useRef(null);
  const [isAssessing, setIsAssessing] = useState(false); // Track assessment state
  const [userAssessment, setUserAssessment] = useState('');
  const [micOn, setMicOn] = useState(false);
  const [assessing, setAssessing] = useState(false);

  useEffect(() => {
    // Scroll to the bottom of the chat container when conversation history changes
    if (chatContainerRef.current) {
      chatContainerRef.current.scrollTop = chatContainerRef.current.scrollHeight;
    }
  }, [conversationHistory]);

  const startListening = () => {
    // if (micOn) {
      recognition.onresult = (event) => {
        const transcript = event.results[0][0].transcript;
        setUserInput(transcript);
        sendToGenerativeAI(transcript);
      };
      recognition.start(); // Start microphone
      console.log('Mic started'); // Add logging for debugging
    // } else {
    //   recognition.stop(); // Stop microphone
    //   console.log('Mic stopped'); // Add logging for debugging
    // }
    setMicOn(!micOn); // Toggle micOn state
  };

  const stopSpeaking = () => {
    window.speechSynthesis.cancel(); // Stop speech synthesis
    setSpeaking(false); // Update state to indicate speech stopped
  };

  const sendToGenerativeAI = async (userInput) => {
    setProcessing(true); // Set processing state to true
    let res;
    try {
      // Make API request to your generative AI model (for conversation)
      const response = await chatService(userInput);
      res = response;
      setAiResponse(response);
      speakResponse(response);
    } catch (error) {
      console.error('Error:', error);
    } finally {
      setConversationHistory([
        ...conversationHistory,
        { messageType: 'user', messageContent: userInput },
        { messageType: 'ai', messageContent: res },
      ]);
      setProcessing(false); // Reset processing state when AI processing is done
    }
  };

  const assessConversation = async () => {
    setIsAssessing(true);
    setAssessing(true);
    // Set assessing state to hide buttons
    // Make API request to your generative AI model (for assessment)
    // Replace with your actual assessment logic
    if(conversationHistory.length === 0){
      setUserAssessment('There is nothing to assess.');
      setAssessing(false);
    }
    else{
    try {
      const assessment = await chatService('Now assess this conversation for grammar and vocabulary.');
      setUserAssessment(assessment);
      const speech = convertTextToSpeech(assessment); // Replace with your speech synthesis function
      speakAssessment(speech);
      setAssessing(false);
    } catch (error) {
      console.error('Error:', error);
    }
  }
  };

  const convertTextToSpeech = (text) => {
    // Replace with your actual text-to-speech conversion logic
    return `<speak>${text}</speak>`; // Example speech synthesis format
  };

  const speakAssessment = (speech) => {
    // Replace with your actual speech playback logic
    console.log(`Assessment speech: ${speech}`); // Placeholder for now
  };

  const speakResponse = (response) => {
    const synth = window.speechSynthesis;
    if (!synth) {
      console.error('Speech synthesis not supported');
      return;
    }
    const utterance = new SpeechSynthesisUtterance(response);
    synth.speak(utterance);
    setSpeaking(true); // Update state to indicate speech started

    utterance.onend = function (event){
      setSpeaking(false); 
    }
  };

  return (
    <div className="main-speaking-container">
      <h4>Press the Say it button and say, <u>'Shall we start!'</u>, to get started.</h4>

      <div className="speaking-container">
        <div ref={chatContainerRef} className="chat-container">
          {/* Chat history rendering */}
          {conversationHistory.map((message, index) => (
            <div
              key={index}
                className={`message ${message.messageType === 'user' ? 'user-message' : 'ai-message'}`}
            >
               {message.messageContent}
             </div>
           ))}
        </div>
        {!isAssessing && ( // Only show buttons if not assessing
          <div className="speaking-button-container">
            <button className="speaking-button" onClick={startListening}>
              {/* {!micOn ? 'Say It' : 'Say It'} */}
              Say It
            </button>
            {/* Conditionally render the "Stop" button based on the speaking state */}
            {speaking && ( // Show Stop button only when AI is speaking
              <button className="speaking-button" onClick={stopSpeaking}>
                Stop
              </button>
            )}
            {/* Show loading indicator */}
            {processing && <SpinnerGap size={32} color="#333" weight="bold" className='loadingIcon' />}
          </div>
        )}
      </div>

      <button className="assess-button" onClick={assessConversation} disabled={isAssessing}>
        {!assessing ? "Assess Conversation" : "Assessing..."}
      </button>
      
      { userAssessment &&(
        <>
        <h4 className="assessment-result-speaking">Assessment</h4> 
        <pre className="assessment-result-speaking">{userAssessment}</pre>
        </>)
      }
    </div>
  );
};

export default Speaking;
