import React, { useState} from 'react'
import {useNavigate} from 'react-router-dom';
import './Login.css';




function Login(props) {

  const [email, setEmail] = useState("");
  const [pass, setPass] = useState("");

  const navigate = useNavigate();

  // const HandleClick=()=>{
  //   navigate("/SignUp");
  // }



  const handleSubmit=(event)=>{

    event.preventDefault();

    props.contextValue.signInWithEmailAndPassword(props.contextValue.auth, email, pass)
    .then((userCredential)=>{
      const user = userCredential.user;
            // ...
            

            const dbRef = props.contextValue.ref(props.contextValue.getDatabase());
            //var user_data= get(db_ref);
            var user_data;
            const starCountRef = props.contextValue.ref(props.contextValue.database, "users/" + user.uid);
                props.contextValue.onValue(starCountRef, (snapshot) => {
                    if (snapshot.exists()) {
                    var data = snapshot.val();
                    const Data = {
                      'name': data.name,
                      'email': data.email,
                      'dob': data.dob,
                      'gender':data.gender
                    }
                    props.setUserData(Data);
                    console.log(Data);
                    console.log(props.userData)
                    // window.localStorage.setItem('_User', user_data);
                    // console.log(user_data);
                    // alert('Account Created Successfully! \nUser Logged In!!')
                    // window.location.replace("afterLoginPage.html");
                } else {
                    console.log("No data available");
                }
              })
      alert( "Logged in!" )
      navigate("/listening-practice")
    }).catch((error)=>{
      const errorCode = error.code;
      const errorMessage = error.message;
      alert("Error: "+ errorCode + " - " + errorMessage);
    })
  }

  
  const handleHome = ()=>{
    navigate('/');
  }

  const handleSignUp=()=>{
    navigate('/SignUp');
  }



  return (
    <div>
    <div className='Login-nav'>
      <p className='Login-p'><h1 className='Login-h1'>FM</h1><h3 className='Login-h3'>Fluency Mentor</h3></p>
      
        <button className='Login-btn' onClick={handleHome} id="btn">Home</button>
        <button className='Login-btn' onClick={handleSignUp} id="btn">SignUp</button>
      
    </div>

    <div className='Login-Form' style={{height: "200px"}}>
      <form >
        <label className='Login-label' htmlFor='inp1'>Email: &nbsp; &nbsp; &nbsp; &nbsp;</label>
        <input className='Login-input' type='email' value={email} id="inp1" onChange={((e)=>setEmail(e.target.value))} />
        <br></br>
        <label className='Login-label' htmlFor='inp2'>Password: &nbsp;</label>
        <input className='Login-input' type='password' id='inp2' value={pass} onChange={((e)=>setPass(e.target.value))} />
        <br></br><br></br>
        <button className='Login-button' onClick={handleSubmit}>Submit</button>
        
        
      </form>
    </div>

    </div>
  )
}

export default Login
