import React, { useState, useEffect } from 'react';
import './Navbar.css';
import { useLocation, useNavigate } from 'react-router-dom';

const Navbar = ({userData}) => {
    const [selectedMenu, setSelectedMenu] = useState('');
    const location = useLocation();
    const navigate = useNavigate();

    // useEffect to update selectedMenu state based on the current URL path
    useEffect(() => {
        const path = location.pathname;
        if (path.includes('listening')) {
            setSelectedMenu('Listening Practice');
        } else if (path.includes('speaking')) {
            setSelectedMenu('Speaking Practice');
        } else if (path.includes('writing')) {
            setSelectedMenu('Writing Practice');
        }
    }, [location.pathname]);

    const handleNavigation = (menu) => {
        setSelectedMenu(menu);
        navigate(`/${menu.toLowerCase().replace(' ', '-')}`);
    };

    return (
        <div>
            <div className='nav'>
                <p className='navi'><h1 className='innerNavi'>FM</h1><h3 className='innerNavi'>Fluency Mentor</h3></p>
                <div style={{marginRight: '20px'}}>
                    <h4 style={{margin: '10px'}}>Welcome</h4> <h4 style={{marginTop: '5px'}}>{userData.name}</h4>
                </div>
            </div>
            <div className='menu'>
                <div className={selectedMenu === 'Listening Practice' ? 'menuItem active' : 'menuItem'} onClick={() => handleNavigation('Listening Practice')}>Listening Practice</div>
                <div className={selectedMenu === 'Speaking Practice' ? 'menuItem active' : 'menuItem'} onClick={() => handleNavigation('Speaking Practice')}>Speaking Practice</div>
                <div className={selectedMenu === 'Writing Practice' ? 'menuItem active' : 'menuItem'} onClick={() => handleNavigation('Writing Practice')}>Writing Practice</div>
            </div>
        </div>
    );
};

export default Navbar;
