import { GoogleGenerativeAI } from "@google/generative-ai";

const API_KEY = "AIzaSyCyoQ-4kPydn89u8mHDTey_WSSyosMBq88";
// Access your API key (see "Set up your API key" above)
const genAI = new GoogleGenerativeAI(API_KEY);

export const chatService= async (Msg)=> {
  // For text-only input, use the gemini-pro model
  const model = genAI.getGenerativeModel({ model: "gemini-pro"});

  const chat = model.startChat({
    history: [
      {
        role: "user",
        parts: [{ text: "Hello, I want you to be my english conversation skills guide, we will have a conversation in english until i say stop and then you will assess  my english grammer and vocabulary. And try to keep the conversation on from your side until i ask you to stop and assess my english. And keep your part to be small" }],
      },
      {
        role: "model",
        parts: [{ text: "Great to meet you. What would you like to know?" }],
      },
    ],
    generationConfig: {
      maxOutputTokens: 1000,
    },
  });
  try {
    const result = await chat.sendMessage(Msg); // Send the message without stringify
    const response = await result.response;
    const text = response.text();
    console.log(text);
    return text;
  } catch (error) {
    console.error("Error during conversation:", error);
    // Handle the error gracefully, like displaying a message to the user
    return "An error occurred. Please try again later.";
  }
}

