import React, { useState, useEffect } from 'react';
import './OutputAudio.css';

const OutputAudio = ({ text }) => {

  const [isPaused, setIsPaused] = useState(false);
  const [utterance, setUtterance] = useState(null);
  const [rate, setRate] = useState(1);
  const [progress, setProgress] = useState(0);
  const [isStopped, setIsStopped] = useState(true); // Track audio stopped state

  useEffect(() => {
    const synth = window.speechSynthesis;
    const u = new SpeechSynthesisUtterance(text);
    u.rate = rate;

    u.onstart = function (event) {
      setProgress(0);
      setIsStopped(false);
    };

    u.onend = function (event) {
      setProgress(100);
      setIsStopped(true);
      setIsPaused(false); // Reset isPaused to false when audio finishes
    };

    u.onboundary = function (event) {
      setProgress((event.charIndex / text.length) * 100);
    };

    setUtterance(u);

    return () => {
      synth.cancel();
      setIsPaused(false); // Reset isPaused to false on cleanup (stop or finish)
    };
  }, [text, rate]);

  const handlePlay = () => {
    const synth = window.speechSynthesis;

    if (isPaused) {
      synth.resume();
    } else {
      synth.speak(utterance);
    }

    setIsPaused(true);
  };

  const handlePause = () => {
    const synth = window.speechSynthesis;
    synth.pause();
    setIsPaused(true);
  };

  const handleStop = () => {
    const synth = window.speechSynthesis;
    synth.cancel();
    setIsPaused(false);
    setIsStopped(true);
    setProgress(0);
  };

  return (
    <div className="output-audio-container">
      <h4>Listen to this Audio Carefully and then Answer the below Questions</h4>
      {isStopped && <progress className="progress-bar" value={0} max="100"></progress>}
      {!isStopped && <progress className="progress-bar" value={progress} max="100"></progress>}
      &nbsp;&nbsp;&nbsp;

      <select
        className="rate-select"
        onChange={(e) => {
          setRate(e.target.value);
          const synth = window.speechSynthesis;
          synth.cancel();
          setIsPaused(false);
          setIsStopped(true);
        }}
      >
        <option value="0.5">0.5x</option>
        <option value="0.75">0.75x</option>
        <option value="1" selected>1.0x</option>
        <option value="1.25">1.25x</option>
        <option value="1.5">1.5x</option>
        <option value="2">2.0x</option>
        <option value="4">4.0x</option>
      </select>

      <div className="button-group">
        <button className="button play-button" onClick={handlePlay}>{isPaused ? 'Resume' : 'Play'}</button>
        <button className="button pause-button" onClick={handlePause}>Pause</button>
        <button className="button stop-button" onClick={handleStop}>Stop</button>
      </div>
    </div>
  );
};

export default OutputAudio;
