import React from 'react'
import './Home.css'
import { useNavigate } from 'react-router-dom'

function Home() {

  const navigate = useNavigate();

  const handleLogin = ()=>{
    navigate('/login');
  }

  const handleSignUp = ()=>{
    navigate('/signup');
  }

  return (
    <div>
      <div className='Home-nav'>
        <p className='Home-p'><h1 className='Home-h1'>FM</h1><h3 className='Home-h3'>Fluency Mentor</h3></p>
        <button className='Home-btn' onClick={handleLogin} id="btn">Login</button>
        <button className='Home-btn' onClick={handleSignUp} id="btn">SignUp</button>
      </div>

      {/* Floating Boxes */}
      <div className="floating-box-container">
        <div className="floating-box">
          <h2 className='floating-box-h2'>Improve Your Listening Skills</h2>
          <p className='floating-box-p'>Enhance your English listening skills with our audio exercises. Listen to engaging audio clips and answer questions based on what you hear. Practice understanding different accents and speech patterns to boost your comprehension abilities.</p>
        </div>
        <div className="floating-box">
          <h2 className='floating-box-h2'>Practice Speaking with AI Bot</h2>
          <p className='floating-box-p'>Hone your English speaking skills through interactive conversations with our AI bot. Engage in realistic dialogues covering various topics and receive instant feedback on pronunciation, fluency, and vocabulary usage. Build confidence and fluency in spoken English effortlessly.</p>
        </div>
        <div className="floating-box">
          <h2 className='floating-box-h2'>Sharpen Your Writing Skills</h2>
          <p className='floating-box-p'>Develop your English writing proficiency with our writing practice feature. Receive prompts on interesting topics and express your thoughts through written paragraphs. Our AI-powered assessment provides feedback on grammar, vocabulary, and coherence, helping you refine your writing skills effectively.</p>
        </div>
      </div>

    </div>
  )
}

export default Home
