import React, { useRef, useState } from 'react'
import { useNavigate } from 'react-router-dom';
import './SignUp.css'


function SignUp(props) {

  const [name, setName] = useState("");
  const [email, setEmail] = useState("");
  const [password, setPassword] = useState("");
  const [dob, setDob] = useState();
  const [gender, setGender] = useState("");

  const inputRef = useRef();

  const navigate = useNavigate();

  

  // const handleClick=()=>{
  //   navigate("/login");
  // }

  const handleFocus=()=>{
    inputRef.current.type='date';
  }

  const handleBlur=()=>{
    inputRef.current.type='text';
  }

  const handleSubmit=(event)=>{
    event.preventDefault();

    props.contextValue.createUserWithEmailAndPassword(props.contextValue.auth, email, password)
    .then((userCredential)=>{
      //signed in
      const user = userCredential.user;
      var database_ref = props.contextValue.ref(props.contextValue.database, "users/" + user.uid);

      var user_data = {
        email: email,
        name: name,
        dob: dob,
        gender: gender
      }

      const actionCodeSettings = {
        url: "https://shrutj.github.io/FluencyMentor/#/login",
        
        handleCodeInApp: true,
      };

      props.contextValue.sendEmailVerification(props.contextValue.auth.currentUser, actionCodeSettings)
         .then(() => {
          alert("Verification email sent!!")
            props.contextValue.set(database_ref, user_data );
            props.contextValue.signInWithEmailAndPassword(props.contextValue.auth, email, password)
            .then((userCredential)=>{

            }).catch((error)=>{
              const errorCode = error.code;
              const errorMessage = error.message;
              alert("Error: "+ errorCode + " - " + errorMessage);
            })
          }
         )
         .catch((error)=>{
          const errorCode = error.code;
        const errorMessage = error.message;
        alert("Error: "+ errorCode + " - " + errorMessage);
         })
      

      


    }).catch((error)=>{
      const errorCode = error.code;
      const errorMessage = error.message;
      alert("Error: "+ errorCode + " - " + errorMessage);
    })

  }

  const handleGoogle = (event) => {
    event.preventDefault();
    props.contextValue.signInWithPopup(props.contextValue.auth, props.contextValue.provider)
      .then((result) => {
        // This gives you a Google Access Token. You can use it to access the Google API.
        // const credential = props.contextValue.GoogleAuthProvider.credentialFromResult(result);
        // const token = credential.accessToken;
        // The signed-in user info.
        const user = result.user;
        console.log(user);
        var database_ref = props.contextValue.ref(props.contextValue.database, "users/" + user.uid);

        setName(user.displayName);
        setEmail(user.email);

        var user_data = {
          email: email,
          name: name,
          phone_number: user.phoneNumber
        };

        props.contextValue.set(database_ref, user_data);
        alert("Account Created!!");
        navigate("/AfterLogin");

        // IdP data available using getAdditionalUserInfo(result)
        // ...

        //     fetch('https://www.googleapis.com/oauth2/v3/userinfo', {
        //   headers: {
        //     Authorization: `Bearer ${token}`,
        //   },
        // })
        //   .then((response) =>{ const x = response.json()
        //   console.log(x)})
        //   .then((data) => {
        //     const user = credential.user;
        //     var database_ref = props.contextValue.ref(props.contextValue.database, "users/" + user.uid);
        //     props.contextValue.set(database_ref, data );
        //     // You can now use the data here.
        //     console.log(data);
        //     alert("Account created!!")
        //   })
        //   .catch((error) => {
        //     console.error('Error:', error);
        //   });

      }).catch((error) => {
        // Handle Errors here.
        const errorCode = error.code;
        const errorMessage = error.message;
        // The email of the user's account used.
        // const email = error.customData.email;
        // // The AuthCredential type that was used.
        // const credential = props.contextValue.GoogleAuthProvider.credentialFromError(error);
        alert("Error: " + errorCode + " - " + errorMessage);
        // ...
      });


  }

  const handleHome = () => {
    navigate('/');
  }

  const handleLogin = () => {
    navigate('/login');
  }



  return (
    <div>
      <div className='SignUp-nav'>
        <p className='SignUp-p'><h1 className='SignUp-h1'>FM</h1><h3 className='SignUp-h3'>Fluency Mentor</h3></p>
        <button className='SignUp-btn' onClick={handleHome} id="btn">Home</button>
        <button className='SignUp-btn' onClick={handleLogin} id="btn">Login</button>

      </div>

      <div style={{ marginBottom: "200px" }}>
        <form className='SignUp-Form'>
          <input className='SignUp-input' id='name' type='text' value={name} placeholder='Name' onChange={(e) => setName(e.target.value)} />
          <br></br>

          <input className='SignUp-input' id='dob' ref={inputRef} type='text' placeholder='DOB' onFocus={handleFocus} onBlur={handleBlur} value={dob} onChange={(e) => setDob(e.target.value)} />
          <br></br>

          <select className='SignUp-select' id='gender' placeholder='Gender' value={gender} onChange={(e) => setGender(e.target.value)} >
            <option value="" disabled selected>Gender</option>
            <option value='Male'>Male</option>
            <option value='Female'>Female</option>
            <option value='Others'>Others</option>
          </select>
          <br></br>

          <input className='SignUp-input' id='email' placeholder='Email' type='email' value={email} onChange={(e) => setEmail(e.target.value)} />
          <br></br>

          <input className='SignUp-input' id='password' placeholder='Password' type='password' value={password} onChange={(e) => setPassword(e.target.value)} />
          <br></br>

          <button id='btn' className='SignUp-Btn' onClick={handleSubmit}>Submit</button>

          <hr></hr>
          <h5 style={{ margin: "0", padding: "0", textAlign: "center" }}>OR</h5>
          <h5 style={{ margin: "0", padding: "0", textAlign: "center" }}>Sign Up using your Google Account</h5>

          <button className='SignUp-Google-Button' onClick={handleGoogle}>Google</button>

        </form>
      </div>
    </div>
  )
}

export default SignUp